import React, { useEffect, useLayoutEffect, useState } from "react";
import { Button, Modal, Tooltip } from "antd";
import {
  CloseOutlined,
  LeftSquareOutlined,
  RightSquareOutlined,
  PlusSquareOutlined,
} from "@ant-design/icons";
import { Layout } from "react-grid-layout";
import _ from "lodash";
import { useLocation } from "react-router-dom";

import {
  CONSTANTS,
  DEFAULT_THEME,
  DIVIDER_TYPES_FN,
  LOCAL_STORAGE,
  TAG_TYPES_FN,
} from "./constants";
import Header from "./components/header";

import MyGridLayout from "./components/gridLayout";
import Tools from "./components/tools";
import { BGIMG } from "@/constants";

import guide from "@/assets/guide.png";

export type MyElement = {
  type: string;
  props: Record<string, any>;
  key?: string | number;
};

export type Data = {
  text: MyElement | string | JSX.Element;
  type: string;
  title?: string;
  theme?: string;
  layout?: Partial<Layout>;
  style?: React.CSSProperties;
  reload?: boolean;
};

export type BgNodes = React.CSSProperties;
export interface EnhanceLayout extends Layout {
  data: Data;
}

export type Layouts = {
  block: Record<string, Data>;
  layout: Layout[];
};

export type JSONData = {
  layouts?: Layouts[];
  theme?: string;
  bgNodes?: React.CSSProperties[];
  bgImg?: string;
};

function App() {
  const [page, setPage] = useState(0);
  const [total, setTotal] = useState(1);
  const [layouts, setLayouts] = useState<Layouts[]>([
    {
      block: {},
      layout: [],
    },
  ]);
  const [theme, setTheme] = useState(DEFAULT_THEME);
  const [bgNodes, setBgNodes] = useState<BgNodes[]>([]);
  const [printNodes, setPrintNodes] = useState<string[]>([]);

  const [guideVisible, setGuideVisible] = useState(false);
  const locationParams = useLocation();

  useLayoutEffect(() => {
    const guideStatus = localStorage.getItem(LOCAL_STORAGE.GUIDE);
    if (guideStatus === LOCAL_STORAGE.GUIDE_STATUS) {
      setGuideVisible(false);
    } else {
      setGuideVisible(true);
    }
  }, []);

  useEffect(() => {
    setLayouts((data) => {
      const arr = [...data];
      Object.keys(data[page].block).forEach((item) => {
        const nodeType = item.split("-")[0];
        const block = layouts[page].block[item];
        switch (nodeType) {
          // divider和tag主题跟随
          case CONSTANTS.DIVIDER:
          case CONSTANTS.TAG: {
            const { type: bType, title } = block;

            const fn =
              nodeType === CONSTANTS.TAG ? TAG_TYPES_FN : DIVIDER_TYPES_FN;

            const { type, props, key } = fn(bType, title, theme);

            arr[page].block[item] = {
              ...block,
              text: {
                type,
                props,
                key,
              },
              theme,
            };
            break;
          }
        }
      });

      return arr;
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [theme, page]);

  const addBlock = ({
    type = "p",
    text = "",
    title,
    theme,
    layout = {},
    style = {},
  }: Data) => {
    const key = layout.i || "p-" + Date.now() + layouts[page].layout.length;
    // 如果没有坐标信息，默认添加到末尾
    if (!layout.y && layout.y !== 0) {
      const lastY = layouts[page].layout.reduce(
        (pre, cur) => Math.max(pre, cur.y),
        0
      );
      layout.y = lastY;
      layout.x = 0;
    }

    layout.i = key;

    setLayouts((layouts) => {
      const arr = _.cloneDeep(layouts);

      arr[page].layout.push(layout as Layout);
      arr[page].block[key] = {
        type,
        text,
        title,
        theme,
        style,
      };

      return arr;
    });
  };

  const handleDeleteBgNode = (i: number) => {
    setBgNodes((n) => {
      const arr = [...n];
      arr.splice(i, 1);
      return arr;
    });
  };

  const handlePre = () => {
    setPrintNodes((v) => {
      const arr = [...v];

      arr[page] = document.querySelector(
        `#${CONSTANTS.PREVIEW_BOX}${page}`
      )!.innerHTML;
      return arr;
    });
    setPage(page - 1);
  };

  const handleNext = () => {
    const node = document.querySelector(`#${CONSTANTS.PREVIEW_BOX}${page}`)!;

    setPrintNodes((v) => {
      const arr = [...v];
      arr[page] = node.innerHTML;
      return arr;
    });
    setPage(page + 1);
    if (page + 1 === total) {
      setLayouts([
        ...layouts,
        {
          layout: [],
          block: {},
        },
      ]);
      setTotal(total + 1);
    }
  };

  const handleCloseGuide = () => {
    setGuideVisible(false);
    localStorage.setItem(LOCAL_STORAGE.GUIDE, LOCAL_STORAGE.GUIDE_STATUS);
  };

  // 背景图片
  const bgImgNode = (
    <img
      src={(locationParams.state as any)?.[BGIMG]}
      alt=""
      style={{
        position: "absolute",
        left: 0,
        top: 0,
        bottom: 0,
        right: 0,
        width: "100%",
        height: "100%",
      }}
    />
  );

  return (
    <div className="App">
      <Header
        addBlock={addBlock}
        theme={theme}
        setTheme={setTheme}
        layouts={layouts}
        setLayouts={setLayouts}
        bgNodes={bgNodes}
        setBgNodes={setBgNodes}
        printNodes={printNodes}
        setPrintNodes={setPrintNodes}
        setTotal={setTotal}
        setPage={setPage}
        page={page}
      />
      <div className="flex justify-around bg-gray-100 py-16 pl-10">
        {/* edit */}
        <Tools
          addBlock={addBlock}
          theme={theme}
          setTheme={setTheme}
          setLayouts={setLayouts}
          setBgNodes={setBgNodes}
          printNodes={printNodes}
          setPrintNodes={setPrintNodes}
          setTotal={setTotal}
          setPage={setPage}
          page={page}
        />
        <div className="w-6/12 flex justify-center relative">
          <div
            className="mx-1 my-4 layout-box bg-white p-2 min-h-screen relative"
            style={{ height: 1050 }}
          >
            {bgImgNode}
            {bgNodes.map((item, i) => (
              <div
                style={{
                  position: "absolute",
                  backgroundColor: theme,
                  ...item,
                }}
                key={i}
              >
                <CloseOutlined
                  className="absolute top-0 right-0 text-localxs cursor-pointer z-10"
                  onClick={() => handleDeleteBgNode(i)}
                />
              </div>
            ))}
            {layouts.map((item, i) => (
              <MyGridLayout
                key={i}
                data={item}
                addBlock={addBlock}
                setLayouts={setLayouts}
                page={page}
                index={i}
              />
            ))}
          </div>
          <Tooltip title="上一页">
            <LeftSquareOutlined
              className="absolute text-gray-300 z-20 left-12"
              style={{
                fontSize: 40,
                top: "40%",
                display: page === 0 ? "none" : "block",
              }}
              onClick={handlePre}
            />
          </Tooltip>

          {page + 1 === layouts.length ? (
            <Tooltip title="新增一页">
              <PlusSquareOutlined
                className="absolute text-gray-300 z-20 right-12"
                style={{
                  fontSize: 40,
                  top: "40%",
                }}
                onClick={handleNext}
              />
            </Tooltip>
          ) : (
            <Tooltip title="下一页">
              <RightSquareOutlined
                className="absolute text-gray-300 z-20 right-12"
                style={{
                  fontSize: 40,
                  top: "40%",
                }}
                onClick={handleNext}
              />
            </Tooltip>
          )}
        </div>
        {/* show */}
        <div
          id={`${CONSTANTS.PREVIEW_BOX}${page}`}
          className="w-6/12  flex justify-center"
        >
          <div
            className="mx-1 my-4 layout-box bg-white p-2 min-h-screen relative"
            style={{ height: 1050 }}
          >
            {bgImgNode}
            <div>
              {bgNodes.map((item) => (
                <div
                  style={{
                    position: "absolute",
                    backgroundColor: theme,
                    ...item,
                  }}
                ></div>
              ))}
              {layouts.map((item, i) => (
                <MyGridLayout
                  key={`show${i}`}
                  data={item}
                  addBlock={addBlock}
                  setLayouts={setLayouts}
                  page={page}
                  index={i}
                  isEdit={false}
                />
              ))}
            </div>
          </div>
        </div>
      </div>
      <Modal
        footer={[
          <Button type="primary" onClick={handleCloseGuide}>
            知道了
          </Button>,
        ]}
        closable={false}
        maskClosable={false}
        visible={guideVisible}
      >
        <img src={guide} alt="" />
      </Modal>
    </div>
  );
}

export default App;
