import { Popover } from "antd";
import { ToolIconClass } from "../tools";

interface Props {
  handleIconClick: (e: React.MouseEvent<HTMLUListElement, MouseEvent>) => void;
}

const icons = [
  "icon-github",
  "icon-facebookfacebook52",
  "icon-email",
  "icon-wo",
  "icon-dianhuatianchong",
  "icon-nvsheng",
  "icon-nansheng",
  "icon-jiaoyu2",
  "icon-xiangmu",
  "icon-rongyu",
  "icon-kaiyuan",
  "icon-nianling",
  "icon-qiandai",
  // "icon-qian",
  "icon-xihuan",
  "icon-ziwopingjia",
  "icon-jinengbiaoqian",
  "icon-gongzuonianxian",
  "icon-gongzuonianxian1",
];

export default function Icons({ handleIconClick }: Props) {
  const iconContent = (
    <ul
      className="flex flex-wrap justify-around w-60"
      onClick={handleIconClick}
    >
      {icons.map((item) => (
        <ol key={item}>
          <i
            title={item}
            className={`iconfont ${item} rounded-full h-10 w-10 m-3 cursor-pointer`}
          ></i>
        </ol>
      ))}
    </ul>
  );

  return (
    <Popover content={iconContent} placement="leftTop">
      <i className={`${ToolIconClass} icon-tubiao1`} />
    </Popover>
  );
}
