import { CopyOutlined, CloseOutlined } from "@ant-design/icons";
import React from "react";
import GridLayout, { Layout } from "react-grid-layout";
import _ from "lodash";
import { CONSTANTS, NO_Edit_TYPE } from "@/page/editor/constants";

import { Data, Layouts } from "@/page/editor";
import Setting from "../setting";

interface Props {
  data: Layouts;
  setLayouts: React.Dispatch<React.SetStateAction<Layouts[]>>;
  addBlock: ({ type, text, layout }: Data) => void;
  page: number;
  index: number;
  isEdit?: boolean;
}

export default function MyGridLayout({
  data,
  addBlock,
  setLayouts,
  page,
  index,
  isEdit = true,
}: Props) {
  const handleLayoutChange = (layouts: Layout[], i: number) => {
    setLayouts((pre) => {
      const arr = [...pre];
      arr[i].layout = layouts;
      return arr;
    });
  };

  const handleBlockChange = (
    key: string,
    e: React.FocusEvent<HTMLParagraphElement>
  ) => {
    const node = e.target as HTMLElement;

    setLayouts((layouts) => {
      const arr = _.cloneDeep(layouts);
      arr[page].block[key].text = node.innerText;

      return arr;
    });
  };

  const handleCopyBlock = (key: string) => {
    const i = data.layout.findIndex((item) => item.i === key);
    i !== -1 &&
      addBlock({
        type: data.block[key].type,
        text: data.block[key].text,
        layout: {
          ...data.layout[i],
          i: data.layout[i].i.split("-")[0] + "-" + Date.now(),
        },
        style: data.block[key].style,
      });
  };

  const handleDeleteBlock = (key: string) => {
    const i = data.layout.findIndex((item) => item.i === key);
    setLayouts((pre) => {
      const arr = [...pre];
      arr[page].layout.splice(i, 1);
      delete arr[page].block[key];
      return arr;
    });
  };

  const isContentEditable = (key: string) => {
    return !NO_Edit_TYPE.includes(key.split("-")[0]);
  };

  const blockStyleFn = (key: string) => {
    // 图片样式直接作用在图片元素
    if (key.includes(CONSTANTS.IMG)) {
      return {};
    }
    return data.block[key]?.style;
  };

  return (
    <GridLayout
      layout={data.layout}
      cols={24}
      width={630}
      margin={[5, 2]}
      rowHeight={20}
      isDraggable={isEdit}
      isResizable={isEdit}
      onLayoutChange={(layouts) => handleLayoutChange(layouts, index)}
      style={{ display: page === index ? "block" : "none", zIndex: 5 }}
    >
      {data.layout.map((subItem) => {
        const key = subItem.i;
        const text = data.block[key]?.text;

        const node =
          typeof text === "string" || React.isValidElement(text)
            ? text
            : React.createElement(text?.type || "", text?.props);

        return isEdit ? (
          <div key={key}>
            <div
              onBlur={(e) => handleBlockChange(key, e)}
              contentEditable={isContentEditable(key)}
              suppressContentEditableWarning
              className="bg-gray-200 h-full leading-normal py-2 px-1"
              style={blockStyleFn(key)}
            >
              {node}
            </div>
            <CopyOutlined
              className="absolute top-0 left-0 text-localxs cursor-pointer"
              onClick={() => handleCopyBlock(key)}
            />
            <Setting setLayouts={setLayouts} layoutKey={key} page={page} />

            <CloseOutlined
              className="absolute top-0 right-0 text-localxs cursor-pointer"
              onClick={() => handleDeleteBlock(key)}
            />
          </div>
        ) : (
          <div key={key}>
            <div
              className=" h-full leading-normal py-2 px-1"
              style={blockStyleFn(subItem.i)}
            >
              {node}
            </div>
          </div>
        );
      })}
    </GridLayout>
  );
}
