import { useState } from "react";

import { BgNodes, Data, JSONData, Layouts } from "../..";
import { CONSTANTS, JSON2JSX_ELEMENT, TYPE } from "@/page/editor/constants";
import Icons from "../icons";
import Themes from "../themes";
import DividerModal from "../divider";
import TagModal from "../tag";
import { handleImportJSON, json2jsx } from "@/utils";
import { useNavigate } from "react-router";
import BgNode from "../bgNode";

interface Props {
  addBlock: ({ type, text, layout }: Data) => void;
  theme: string;
  setTheme: React.Dispatch<React.SetStateAction<string>>;
  setLayouts: React.Dispatch<React.SetStateAction<Layouts[]>>;
  setBgNodes: React.Dispatch<React.SetStateAction<BgNodes[]>>;
  setPrintNodes: React.Dispatch<React.SetStateAction<string[]>>;
  setTotal: React.Dispatch<React.SetStateAction<number>>;
  setPage: React.Dispatch<React.SetStateAction<number>>;
  printNodes: string[];
  page: number;
}

export const ToolIconClass =
  "text-2xl iconfont pt-3 pl-2.5 text-white cursor-pointer";

export default function Tools({
  addBlock,
  theme,
  setTheme,
  setBgNodes,
  setLayouts,
  setTotal,
  setPage,
}: Props) {
  const [dividerModalVisible, setDividerModalVisible] = useState(false);
  const [tagModalVisible, setTagModalVisible] = useState(false);
  const navigate = useNavigate();

  const handleAddBlock = (type: string) => {
    if (type === TYPE.DIVIDER) {
      setDividerModalVisible(true);

      return;
    }
    if (type === TYPE.TAG) {
      setTagModalVisible(true);

      return;
    }
    addBlock({
      type,
      text: "",
      layout: {
        w: 6,
        h: 2,
      },
    });
  };

  const handleIconClick = (
    e: React.MouseEvent<HTMLUListElement, MouseEvent>
  ) => {
    const icon = (e.target as HTMLUListElement).title;
    addBlock({
      type: "icon",
      text: {
        type: "i",
        props: {
          className: `iconfont ${icon}`,
        },
      },
      layout: {
        i: `${CONSTANTS.ICON}-${Date.now()}`,
        w: 2,
        h: 2,
      },
    });
  };

  const handleUploadPicture = () => {
    var reads = new FileReader();
    const input = document.getElementById("uploadImage") as HTMLInputElement;
    const f = input.files?.[0] as File;

    reads.readAsDataURL(f);
    reads.onload = (e) => {
      addBlock({
        type: "img",
        text: {
          type: "img",
          props: {
            className: "h-full",
            src: e.target?.result,
            alt: "",
          },
        },
        layout: {
          i: `${CONSTANTS.IMG}-${Date.now()}`,
          w: 6,
          h: 6,
        },
      });
    };
  };

  const handleThemeClick = (
    e: React.MouseEvent<HTMLUListElement, MouseEvent>
  ) => {
    const color = (e.target as HTMLUListElement).title;
    setTheme(color);
  };

  const importJSONCallback = (data: JSONData) => {
    let { layouts = [], theme = "", bgNodes = [], bgImg } = data;
    layouts = layouts.map((item) => {
      Object.keys(item.block).forEach((subItem) => {
        const key = subItem.split("-")[0];

        if (JSON2JSX_ELEMENT.includes(key)) {
          item.block[subItem].text = json2jsx(
            item.block[subItem].text as JSX.Element
          );
        }
      });
      return item;
    });
    setLayouts(layouts);
    setTheme(theme);
    setBgNodes(bgNodes);
    setTotal(layouts.length);
    setPage(0);
    if (bgImg) {
      navigate(".", {
        state: { bgImg },
      });
    }
  };

  return (
    <>
      <div className="fixed z-10 top-12 left-0 w-12 h-full pt-10 flex flex-col bg-gray-900 ">
        <i
          className={`${ToolIconClass} icon-kuaibuju`}
          onClick={() => handleAddBlock(TYPE.P)}
        />
        <i
          className={`${ToolIconClass} icon-biaoti`}
          onClick={() => handleAddBlock(TYPE.DIVIDER)}
        />
        <i
          className={`${ToolIconClass} icon-24gl-tags4`}
          onClick={() => handleAddBlock(TYPE.TAG)}
        />
        <Icons handleIconClick={handleIconClick} />
        <Themes handleThemeClick={handleThemeClick} />
        <div className="mt-2">
          <input
            className="hidden"
            accept="image/*"
            id="uploadImage"
            onChange={handleUploadPicture}
            type="file"
          />
          <label htmlFor="uploadImage">
            <i className={`${ToolIconClass} icon-tupian`} />
          </label>
        </div>
        <BgNode setBgNodes={setBgNodes} />

        <i
          className={`${ToolIconClass} icon-jurassic_import-form absolute bottom-20 left-0 z-10`}
          onClick={() => handleImportJSON(importJSONCallback)}
        />
      </div>
      <DividerModal
        visible={dividerModalVisible}
        setVisible={setDividerModalVisible}
        addBlock={addBlock}
        theme={theme}
      />
      <TagModal
        visible={tagModalVisible}
        setVisible={setTagModalVisible}
        addBlock={addBlock}
        theme={theme}
      />
    </>
  );
}
