import { Dropdown, Menu } from "antd";
import { BgNodes } from "../..";
import { BG_POSITION } from "@/page/editor/constants";
import { ToolIconClass } from "../tools";

interface Props {
  setBgNodes: React.Dispatch<React.SetStateAction<BgNodes[]>>;
}

export default function BgNode({ setBgNodes }: Props) {
  const handleAddBgBlock = (e: any) => {
    let style: React.CSSProperties = {
      width: "calc(100% - 1rem)",
      height: "11.5rem",
    };
    switch (e.key) {
      case BG_POSITION.LEFT:
        style = {
          width: "11.5rem",
          height: "calc(100% - 1rem)",
        };
        break;
      case BG_POSITION.BOTTOM:
        style = {
          ...style,
          bottom: "0.5rem",
        };
        break;
      case BG_POSITION.RIGHT:
        style = {
          width: "11.5rem",
          height: "calc(100% - 1rem)",
          right: "0.5rem",
        };
        break;
    }

    setBgNodes((v) => {
      return [...v, style];
    });
  };

  const bgBlocks = (
    <Menu
      onClick={handleAddBgBlock}
      items={[
        {
          label: "上背景",
          key: BG_POSITION.TOP,
        },
        {
          label: "左背景",
          key: BG_POSITION.LEFT,
        },
        {
          label: "右背景",
          key: BG_POSITION.RIGHT,
        },
        {
          label: "下背景",
          key: BG_POSITION.BOTTOM,
        },
      ]}
    />
  );

  return (
    <Dropdown overlay={bgBlocks}>
      <i className={`${ToolIconClass} icon-dikuai`} />
    </Dropdown>
  );
}
