import { SettingOutlined } from "@ant-design/icons";
import { Form, Popconfirm, Radio } from "antd";
import { Layouts, MyElement } from "../..";

interface Props {
  setLayouts: React.Dispatch<React.SetStateAction<Layouts[]>>;
  layoutKey: string;
  page: number;
}

export default function Setting({ setLayouts, layoutKey, page }: Props) {
  const [form] = Form.useForm();

  const handleOk = () => {
    setLayouts((data) => {
      const arr = [...data];

      let v = arr[page].block[layoutKey];

      arr[page].block[layoutKey] = {
        ...v,
        style: form.getFieldsValue() || {},
      };

      if (layoutKey.includes("img")) {
        (v.text as MyElement).props.style = form.getFieldsValue() || {};
      }

      return arr;
    });
  };

  const content = (
    <Form form={form}>
      {layoutKey.includes("img") ? (
        <>
          <Form.Item name="borderRadius" label="图片形状">
            <Radio.Group
              options={[
                {
                  label: "正方形",
                  value: "0",
                },
                {
                  label: "圆形",
                  value: "50%",
                },
              ]}
            />
          </Form.Item>
          <Form.Item name="border" label="外边框">
            <Radio.Group
              options={[
                {
                  label: "是",
                  value: "10px solid #fff",
                },
                {
                  label: "否",
                  value: "0",
                },
              ]}
            />
          </Form.Item>
          <Form.Item name="borderColor" label="外边框颜色">
            <Radio.Group
              options={[
                {
                  label: (
                    <span className="inline-block h-3 w-3  bg-yellow-500" />
                  ),
                  value: "#F59E0B",
                },
                {
                  label: <span className="inline-block h-3 w-3  bg-gray-300" />,
                  value: "#D1D5DB",
                },
                {
                  label: <span className="inline-block h-3 w-3  bg-red-400" />,
                  value: "#F87171",
                },
                {
                  label: (
                    <span className="inline-block h-3 w-3  bg-green-300" />
                  ),
                  value: "#6EE7B7",
                },
              ]}
            />
          </Form.Item>
        </>
      ) : (
        <>
          <Form.Item name="color" label="字体颜色">
            <Radio.Group
              options={[
                {
                  label: "白色",
                  value: "#fff",
                },
                {
                  label: "黑色",
                  value: "#000",
                },
                {
                  label: "灰色",
                  value: "#ccc",
                },
              ]}
            />
          </Form.Item>
          <Form.Item label="字体大小" name="fontSize">
            <Radio.Group
              options={[
                {
                  label: "正常",
                  value: "1rem",
                },
                {
                  label: "小号",
                  value: "1.1rem",
                },
                {
                  label: "中号",
                  value: "1.2rem",
                },
                {
                  label: "大号",
                  value: "1.3rem",
                },
              ]}
            />
          </Form.Item>
          <Form.Item label="字体粗细" name="fontWeight">
            <Radio.Group
              options={[
                {
                  label: "正常",
                  value: "",
                },
                {
                  label: "加粗",
                  value: "500",
                },
              ]}
            />
          </Form.Item>
          <Form.Item label="字体位置" name="textAlign">
            <Radio.Group
              options={[
                {
                  label: "左对齐",
                  value: "left",
                },
                {
                  label: "居中",
                  value: "center",
                },
                {
                  label: "右对齐",
                  value: "right",
                },
              ]}
            />
          </Form.Item>
          <Form.Item label="背景颜色" name="background">
            <Radio.Group
              options={[
                {
                  label: (
                    <span className="inline-block h-3 w-3  bg-yellow-500" />
                  ),
                  value: "#F59E0B",
                },
                {
                  label: <span className="inline-block h-3 w-3  bg-gray-300" />,
                  value: "#D1D5DB",
                },
                {
                  label: <span className="inline-block h-3 w-3  bg-red-400" />,
                  value: "#F87171",
                },
                {
                  label: (
                    <span className="inline-block h-3 w-3  bg-green-300" />
                  ),
                  value: "#6EE7B7",
                },
              ]}
            />
          </Form.Item>
        </>
      )}
    </Form>
  );
  return (
    <Popconfirm
      title={content}
      onConfirm={handleOk}
      icon={null}
      // onCancel={cancel}
      okText="确定"
      cancelText="取消"
    >
      <SettingOutlined
        className="absolute top-0 left-3 text-localxs cursor-pointer"
        // onClick={() => handleSetBlock(item.i)}
      />
    </Popconfirm>
  );
}
