import { Form, Input, Modal, Radio, Space } from "antd";

import { CONSTANTS, TAG_TYPES, TAG_TYPES_FN } from "@/page/editor/constants";
import { Data } from "@/page/editor";

interface Props {
  visible: boolean;
  setVisible: React.Dispatch<React.SetStateAction<boolean>>;
  addBlock: ({ type, text, layout }: Data) => void;
  theme?: string;
}

export default function Tag({ visible, setVisible, addBlock, theme }: Props) {
  const [form] = Form.useForm();
  const handleOk = () => {
    form.validateFields().then((v) => {
      const { type, props, key } = TAG_TYPES_FN(v.type, v.title, theme);
      addBlock({
        type: v.type,
        title: v.title,
        layout: {
          i: `${CONSTANTS.TAG}-${Date.now()}`,
          w: 3,
          h: 2,
        },
        text: {
          type,
          props,
          key,
        },
      });
      setVisible(false);
    });
  };

  const handleCancel = () => {
    setVisible(false);
  };

  return (
    <Modal
      title="Tag标签"
      visible={visible}
      onOk={handleOk}
      onCancel={handleCancel}
      okText={"确定"}
      cancelText={"取消"}
    >
      <Form form={form}>
        <Form.Item label="tag标题" name="title" rules={[{ required: true }]}>
          <Input />
        </Form.Item>
        <Form.Item noStyle shouldUpdate>
          {({ getFieldValue }) => {
            const v = getFieldValue("title");
            return (
              <Form.Item
                label="tag样式"
                name="type"
                initialValue={TAG_TYPES.BASIC}
              >
                <Radio.Group>
                  <Space direction="vertical">
                    <Radio value={TAG_TYPES.BASIC}>
                      <div style={{ width: 350 }}>
                        {TAG_TYPES_FN(TAG_TYPES.BASIC, v)}
                      </div>
                    </Radio>
                    <Radio value={TAG_TYPES.BASIC_BG}>
                      <div style={{ width: 350 }}>
                        {TAG_TYPES_FN(TAG_TYPES.BASIC_BG, v)}
                      </div>
                    </Radio>
                    <Radio value={TAG_TYPES.RECTANGLE}>
                      <div style={{ width: 350 }}>
                        {TAG_TYPES_FN(TAG_TYPES.RECTANGLE, v)}
                      </div>
                    </Radio>
                    <Radio value={TAG_TYPES.RECTANGLE_BG}>
                      <div style={{ width: 350 }}>
                        {TAG_TYPES_FN(TAG_TYPES.RECTANGLE_BG, v)}
                      </div>
                    </Radio>
                  </Space>
                </Radio.Group>
              </Form.Item>
            );
          }}
        </Form.Item>
      </Form>
    </Modal>
  );
}
