const PRINT_ID = "print-container";

function setStyle() {
  const styleContent = `
    @media print {
      body > :not(#${PRINT_ID}) {
        display: none;
      }
      html,
      body {
        display: block !important;
        margin: 1cm;
      }
      @page {
        size: A4 portrait;
        margin: 1cm 0 1cm 1cm;
        padding: 0;
      }
      .footnotes {
        page-break-after: always;
      }
      #${PRINT_ID}: {
        display: block;
      }
    }
  `;
  const style = document.createElement("style");
  style.innerHTML = styleContent;
  return style;
}

function cleanPrint() {
  const div = document.querySelector(`#${PRINT_ID}`);
  if (!!div) {
    document.body.removeChild(div);
  }
}

function getContainer(nodes: string[]) {
  cleanPrint();
  const container = document.createElement("div");
  container.setAttribute("id", PRINT_ID);

  container.innerHTML =
    nodes.length > 1 ? nodes.join('<div class="footnotes"></div>') : nodes[0];

  return container;
}

function afterAllLoad(container: HTMLDivElement) {
  const imgs = Array.from(container.querySelectorAll("img"));

  if (!imgs.length) {
    return Promise.resolve();
  }

  let loadCount = 0;

  return new Promise<void>((resolve) => {
    const checkFn = () => {
      loadCount++;
      if (loadCount === imgs.length) {
        resolve();
      }
    };

    imgs.forEach((img) => {
      img.addEventListener("load", checkFn);
      img.addEventListener("error", checkFn);
    });
  });
}

export default async function print(nodes: string[]) {
  const style = setStyle();
  const container = getContainer(nodes);

  document.body.appendChild(style);
  document.body.appendChild(container);

  await afterAllLoad(container);
  window.print();

  document.body.removeChild(style);
  document.body.removeChild(container);
}
