import { Form, Input, Modal, Radio, Space } from "antd";

import {
  DIVIDER_TYPES,
  DIVIDER_TYPES_FN,
  CONSTANTS,
} from "@/page/editor/constants";
import { Data } from "@/page/editor";

interface Props {
  visible: boolean;
  setVisible: React.Dispatch<React.SetStateAction<boolean>>;
  addBlock: ({ type, text, layout }: Data) => void;
  theme?: string;
}

export default function Divider({
  visible,
  setVisible,
  addBlock,
  theme,
}: Props) {
  const [form] = Form.useForm();
  const handleOk = () => {
    form.validateFields().then((v) => {
      const { type, props, key } = DIVIDER_TYPES_FN(v.type, v.title, theme);

      addBlock({
        type: v.type,
        title: v.title,
        layout: {
          i: `${CONSTANTS.DIVIDER}-${Date.now()}`,
          w: 24,
          h: 2,
        },
        text: {
          type,
          props,
          key,
        },
      });
      setVisible(false);
    });
  };

  const handleCancel = () => {
    setVisible(false);
  };

  return (
    <Modal
      title="分割线"
      visible={visible}
      onOk={handleOk}
      onCancel={handleCancel}
      okText={"确定"}
      cancelText={"取消"}
    >
      <Form form={form}>
        <Form.Item label="分割线标题" name="title" rules={[{ required: true }]}>
          <Input />
        </Form.Item>
        <Form.Item noStyle shouldUpdate>
          {({ getFieldValue }) => {
            const v = getFieldValue("title");
            return (
              <Form.Item
                label="分割线样式"
                name="type"
                initialValue={DIVIDER_TYPES.LINE_BOTTOM}
              >
                <Radio.Group>
                  <Space direction="vertical">
                    {Object.values(DIVIDER_TYPES).map((item) => (
                      <Radio value={item}>
                        <div style={{ width: 350 }}>
                          {DIVIDER_TYPES_FN(item, v)}
                        </div>
                      </Radio>
                    ))}
                  </Space>
                </Radio.Group>
              </Form.Item>
            );
          }}
        </Form.Item>
      </Form>
    </Modal>
  );
}
