import { message } from "antd";
import { JSONData } from "../page/editor";

/**
 *url2base64
 * @param url 图片路径
 * @param ext 图片格式
 */
export function getUrlBase64(
  url: string,
  ext: string = "jpeg"
): Promise<string> {
  return new Promise((resolve, reject) => {
    const canvas = document.createElement("canvas");
    const ctx = canvas.getContext("2d") as CanvasRenderingContext2D;
    const img = new Image();
    img.src = url;

    img.onload = function () {
      canvas.height = img.height;
      canvas.width = img.width;
      ctx.drawImage(img, 0, 0, img.width, img.height);
      const dataURL = canvas.toDataURL("image/" + ext);
      resolve(dataURL);
      canvas.remove();
    };

    img.onerror = () => reject("加载失败");
  });
}

/**
 *深拷贝
 * @param url 图片路径
 * @param ext 图片格式
 */
export function deepCopy(obj: any) {
  let result: any;
  if (typeof obj === "object") {
    result = obj.constructor === Array ? [] : {};
    for (let i in obj) {
      result[i] = typeof obj[i] === "object" ? deepCopy(obj[i]) : obj[i];
    }
  } else {
    result = obj;
  }
  return result;
}

/**
 *给json加reactelement的symbol
 */

export function json2jsx(data: JSX.Element) {
  const { type, props, key } = data;
  const res: React.ReactElement<any, any> = {
    type,
    props,
    key,
  };
  if (props?.children) {
    if (Array.isArray(props.children)) {
      props.children.forEach((item: any) => {
        if (typeof item !== "string" && !item["$$typeof"]) {
          item["$$typeof"] = Symbol.for("react.element");
          json2jsx(item);
        }
      });
    } else {
      if (typeof props.children !== "string" && !props.children["$$typeof"]) {
        props.children["$$typeof"] = Symbol.for("react.element");
        json2jsx(props.children);
      }
    }
  }
  return res;
}

/**
 *16进制颜色转rgba
 * @param colorStr 16进制颜色
 * @param opacity 透明度
 */
export function colorRgb(colorStr: string, opacity?: number) {
  //十六进制颜色值的正则表达式
  let reg = /^#([0-9a-fA-f]{3}|[0-9a-fA-f]{6})$/;
  let sColor = colorStr.toLowerCase();
  if (sColor && reg.test(sColor)) {
    if (sColor.length === 4) {
      let sColorNew = "#";
      for (let i = 1; i < 4; i += 1) {
        sColorNew += sColor.slice(i, i + 1).concat(sColor.slice(i, i + 1));
      }
      sColor = sColorNew;
    }
    //处理六位的颜色值f
    const colorArr = [];
    for (let i = 1; i < 7; i += 2) {
      colorArr.push(parseInt("0x" + sColor.slice(i, i + 2)));
    }
    if (opacity !== 0 && !opacity) {
      opacity = 1;
    }
    colorArr.push(opacity);
    return "rgba(" + colorArr.join(",") + ")";
  } else {
    return sColor;
  }
}

/**
 *解决JSON.stringify死循环的问题
 */
export const getCircularReplacer = () => {
  return (key: string, value: any) => {
    const arr = ["elementType", "$$typeof", "Provider", "_owner"];
    if (arr.includes(key)) {
      return;
    }
    return value;
  };
};

interface ExportParams {
  name: string;
  data: JSONData;
}
export const handleExportJSON = ({ name, data }: ExportParams) => {
  const elementA = document.createElement("a");

  elementA.download = name;
  elementA.style.display = "none";

  const blob = new Blob([JSON.stringify(data, getCircularReplacer())]);

  elementA.href = URL.createObjectURL(blob);
  document.body.appendChild(elementA);
  elementA.click();
  document.body.removeChild(elementA);
};

type ImportParams = (p: JSONData) => void;

export const handleImportJSON = (cb: ImportParams) => {
  // 创建一个file input
  let input: HTMLInputElement | null = document.createElement("input");
  input.type = "file";
  input.accept = ".json";
  // 绑定onchange事件
  input.onchange = (event: any) => {
    let files = event.target.files;
    if (!files || !files.length) {
      input = null;
      throw new Error("No files");
    }

    // 当选择文件后，使用FileReader API读取文件，返回数据
    let reader = new FileReader();
    reader.onload = (event: any) => {
      try {
        let config = JSON.parse(event.target.result);
        cb(config);
        return config;
      } catch (e: any) {
        message.error(e);
        throw new Error(e);
      }
    };
    reader.readAsText(files[0]);
  };

  // 触发上传文件
  input.click();
};

/**
 *等待所有图片加载完成
 */
export const waitImgsLoaded = async (root: Element) => {
  const imgs =
    root instanceof HTMLImageElement ? [root] : root.querySelectorAll("img");

  return await Promise.all(
    Array.from(imgs).map(
      (img) =>
        new Promise((resolve) => {
          if (img.complete) {
            resolve(img);
            return;
          }
          img.addEventListener("load", () => resolve(img));
        })
    )
  );
};
