import { Modal } from "antd";
import Shoukuan from "@/assets/shoukuan.jpeg";

interface Props {
  visible: boolean;
  setVisible: React.Dispatch<React.SetStateAction<boolean>>;
}

export default function ToGithub({ visible, setVisible }: Props) {
  const handleOk = () => {
    setVisible(false);
  };

  const handleCancel = () => {
    setVisible(false);
  };

  return (
    <Modal
      title="打扰啦"
      visible={visible}
      onOk={handleOk}
      onCancel={handleCancel}
      footer={null}
    >
      <p>如果该项目对您有帮助，请点个star让作者知道吧(:</p>
      <a
        className="pointer text-blue-300"
        target={"__blank"}
        href="https://github.com/MasonEast/MasonEast.github.io"
      >
        去往Github
      </a>
      <p className=" mt-4 mb-1">也可以打赏作者喝个咖啡哟❤️</p>
      <img className="w-40 h-40" src={Shoukuan} alt="" />
    </Modal>
  );
}
